.checkbox {
  position: relative;
  padding-left: 1.875rem;
  cursor: pointer;
  display: flex;
  margin-bottom: 1.5625rem;
}

.checkbox__control {
  position: absolute;
  opacity: 0;
  overflow: hidden;
  visibility: hidden;
}

.checkbox__control:checked + .checkbox__control-fake {
  border: 0.125rem solid #31191F;
}

.checkbox__control:checked + .checkbox__control-fake::before {
  opacity: 1;
}

.checkbox__control:checked ~ .checkbox__label {
  color: #2DA4A0;
  font-weight: 600;
}

.checkbox__control-fake {
  width: 15px;
  height: 15px;
  position: absolute;
  top: 0.375rem;
  left: 0;
  border: 2px solid #31191F;
  transition: border 0.3s;
  border-radius: 50%;
}

.checkbox__control-fake::before {
  content: "";
  width: 9px;
  height: 9px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -4.5px;
  margin-left: -4.5px;
  background: #2DA4A0;
  transition: opacity 0.3s;
  opacity: 0;
  border-radius: 50%;
}

.checkbox__label {
  font-weight: normal;
  padding: 0.1875rem 0;
  font-size: 1.125rem;
  line-height: 150.6%;
  color: #5D3A43;
  transition: color 0.3s;
}

